import { useSelector } from "../../services/hooks";

import ModalStyles from "./Modal.module.css";
import tick from "../../images/tick.gif";
import loading from "../../images/loading.svg";

function OrderDetails() {
  const orderData = useSelector((state) => state.burgerConstructor.orderData);

  return (
    <div className={`${ModalStyles.modal__contentBox} pt-30 pb-30`}>
      <h2
        className={`${ModalStyles.modal__title_shadow} text text_type_digits-large mb-8`}
      >
        {orderData.order.number}
      </h2>
      <p
        className={`${ModalStyles.modal__subtitle} text text_type_main-medium mb-15`}
      >
         
      </p>
      <div className={`${ModalStyles.modal__imgContainer} mb-15`}>
        <img
          src={tick || loading}
          alt="tick animation"
          className={ModalStyles.modal__image}
        />
      </div>
      <div className={ModalStyles.modal__textContainer}>
        <p
          className={`${ModalStyles.modal__text} text text_type_main-small mb-2`}
        >
             
        </p>
        <p
          className={`${ModalStyles.modal__text} text text_type_main-default text_color_inactive`}
        >
              
        </p>
      </div>
    </div>
  );
}

export default OrderDetails;
